import Promise from '../libs/js/bluebird' //

/**
 * code
 * @param {*} resolve
 * @param {*} res data
 */
const checkCode = (resolve, res, reject) => {
	if (res.code === 0) {
		resolve(res);
	} else {
		uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 3000,
			mask: true
		});
		reject(res);
	}
};

const urlParam = function(obj) {
	var arr = [];
	for (var k in obj) {
		if (obj[k] != null && obj[k] != '') {
			arr.push(k + '=' + obj[k]);
		}
	}
	return arr.join('&');
};

/**
 * API
 * @param  {String} api    api 
 * @param  {String} path   
 * @param  {Object} params 
 * @return {Promise}       Promise
 */


export default function(methods, path, params, isNeedToken) {
	uni.showLoading({title: '...',});
	let header = {
		"Content-Type": "application/x-www-form-urlencoded"
	};
	var token = uni.getStorageSync('token');
	if (!!token && isNeedToken) {
		params['token'] = token;
	}
    
	return new Promise((resolve, reject) => {
		uni.request({
			url: path,
			data: params,
			method: methods,
			timeout: '5000',
			header,
			success: function(res) {
				uni.hideLoading();
				checkCode(resolve, res.data, reject);
			},
			fail: function() {
				uni.hideLoading();
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 3000,
					mask: true
				});

			}
		})
	})
};



